import React, { useState } from "react";
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
} from "reactstrap";
import Vendors from "../PurchaseManagement/MasterData/Vendor/";
import PurchaseInvoice from "./PurchaseProcess/PurchaseInvoice";
import Page from "../../components/Page";

const FinancePurchaseProcess = () => {
  const [activeTab, setActiveTab] = useState("1");

  const toggle = (tab) => {
    if (activeTab !== tab) setActiveTab(tab);
  };

  return (
    <div>
      <Page>
        <Nav tabs className="mb-2">
          <NavItem>
            <NavLink
              className={
                activeTab === "2" ? "activepageTabBlue" : "notactivePageTabBlue"
              }
              onClick={() => {
                toggle("2");
              }}
            >
              Purchase Invoice
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={
                activeTab === "1" ? "activepageTabBlue" : "notactivePageTabBlue"
              }
              onClick={() => {
                toggle("1");
              }}
            >
              Vendors
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={activeTab}>
          <TabPane tabId="2">
            <Row>
              <Col>
                <PurchaseInvoice />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="1">
            <Row>
              <Col>
                <Vendors />
              </Col>
            </Row>
          </TabPane>
        </TabContent>
      </Page>
    </div>
  );
};

export default FinancePurchaseProcess;
